import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { TableState } from '../core/table-state';
import { AuthUserService } from '../core/auth-user.service';
import { SubcategoryService } from '../core/subcategory.service';
import { CategoryService } from '../core/category.service';

@Injectable()
export class SubcategoryManagerService {

  public state: TableState = {
    page: 1,
    pageSize: 10,
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  }

  private _total$ = new BehaviorSubject<number>(1);
  get total$(): Observable<number> {
    return this._total$.asObservable();
  }

  private originalList = [];

  private _subcategoryList$ = new BehaviorSubject<any[]>(null);
  get subcategoryList$(): Observable<any[]> {
    return this._subcategoryList$.asObservable();
  }

  private _categoryList$ = new BehaviorSubject<any[]>(null);
  get categoryList$(): Observable<any[]> {
    return this._categoryList$.asObservable();
  }


  constructor(private subcategoryService: SubcategoryService,
    private authUserService: AuthUserService,
    private categoryService: CategoryService) { 
    }

  addSubcategory(subcategory: any): Observable<any> {
    // const subcategory = {
    //   name: subcategory['name'],
    //   email: subcategory['email'],
    //   password: subcategory['password'],
    //   address: subcategory['address'],
    //   city: subcategory['city'],
    //   mobile: subcategory['mobile'],
    //   status: subcategory['status']
    // };
    return this.subcategoryService.addSubcategory(subcategory);
  }

  updateSubcategory(subcategory: any): Observable<any> {
    // const subcategory = {
    //   _id: subcategory['_id'],
    //   name: subcategory['name'],
    //   email: subcategory['email'],
    //   address: subcategory['address'],
    //   city: subcategory['city'],
    //   mobile: subcategory['mobile'],
    //   status: subcategory['status']
    // };
    return this.subcategoryService.updateSubcategory(subcategory);
  }

  deleteSubcategory(id: string): Observable<any> {
    return this.subcategoryService.deleteSubcategory(id);
  }

  getSubcategory(id: string): Observable<any> {
    return this.subcategoryService.getSubcategory(id);
  }


  getSubcategoryList(): void {

    this.subcategoryService.getSubcategoryList(this.authUserService.user['vendor_id'])
      .subscribe((data: any[]) => {
        console.log('Got _subcategoryList for you: ', data);
        this._subcategoryList$.next(data);
        this._total$.next(data.length);
        this.originalList = data;
      });
  }

  getCategoryList(): void {
    this.categoryService.getCategoryList(this.authUserService.user['vendor_id'])
      .subscribe((data: any[]) => {
        console.log('Got _categoryList for you: ', data);
        this._categoryList$.next(data);
      });
  }

  searchText(): void {
    if (!this.state.searchTerm) {
      this._subcategoryList$.next(this.originalList);
      this._total$.next(this.originalList.length)
    } else {
      const searchTerm = this.state.searchTerm.toLocaleLowerCase();
      const filteredList = this.originalList.filter((item: any) => item['name'] && item['name'].toLocaleLowerCase().includes(searchTerm) ||
      item['category'] && item['category']['name'] && item['category']['name'].toLocaleLowerCase().includes(searchTerm) ||
        item['status'] && item['status'].toLocaleLowerCase().includes(searchTerm));
      this._subcategoryList$.next(filteredList);
      this._total$.next(filteredList.length);
    }
  }
}
