import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Subject } from 'rxjs';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { SubcategoryManagerService } from '../subcategory-manager.service';
import { takeUntil } from 'rxjs/operators';
import { AuthUserService } from 'src/app/core/auth-user.service';

@Component({
  selector: 'app-add-subcategory',
  templateUrl: './add-subcategory.component.html'
})
export class AddSubcategoryComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();
  categoryForm: FormGroup;
  logginIn = false;
  error: string;

  @Input()
  subcategory: any;

  @Input()
  operation: 'CREATE' | 'UPDATE' = 'CREATE';
  categoryList$ = this.categoryManager.categoryList$;
  constructor(
    // private router: Router,
     private formBuilder: FormBuilder,
     private activeModal: NgbActiveModal,
     private categoryManager: SubcategoryManagerService,
    private authUserService: AuthUserService

  ) {
  }
  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }

  ngOnInit(): void {
    this.categoryForm = this.formBuilder.group({
      subcategory_id : this.subcategory.subcategory_id,
      name: [this.subcategory.name, Validators.required],
      category: [this.subcategory.category, [Validators.required]],
      status: [this.subcategory.status, Validators.required],
      owner_id: [this.authUserService.user['vendor_id'], Validators.required]
    });
  }

  submit(): void {
    const subcategory = this.categoryForm.value;
    try {
      subcategory.category = typeof subcategory.category === 'string'? JSON.parse(subcategory.category): subcategory.category;
    } catch (error) {
      throw(error);
    }
    if (this.operation == 'CREATE') {
      this.categoryManager.addSubcategory(subcategory)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe((data: any) => {
          this.activeModal.close(true);
          console.log('data: ', data.vendor);
        });
    } else {
      this.categoryManager.updateSubcategory(subcategory)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe((data: any) => {
          this.activeModal.close(true);
          console.log('data: ', data.vendor);
        });
    }
  }

  cancel(): void {
    this.activeModal.close(false);
  }

}
