import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject, from } from 'rxjs';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { SubcategoryManagerService } from '../subcategory-manager.service';
import { AddSubcategoryComponent } from '../add-subcategory/add-subcategory.component';
import { filter, takeUntil } from 'rxjs/operators';
import { ConfirmDeleteComponent } from '../confirm-delete/confirm-delete.component';

@Component({
  selector: 'app-subcategory-list',
  templateUrl: './subcategory-list.component.html'
})
export class SubcategoryListComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();

  constructor(public subcategoryManagerService: SubcategoryManagerService, private ModalService: NgbModal) {
    this.subcategoryManagerService.getCategoryList();
  }

  ngOnInit(): void {
    this.subcategoryManagerService.getSubcategoryList();
  }

  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }


  addSubcategoryModal(): void {
    const modalRef = this.ModalService.open(AddSubcategoryComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    const subcategory = {
      subcategory: '',
      status: 'Active'
    }
    modalRef.componentInstance.subcategory = subcategory;
    modalRef.componentInstance.operation = 'CREATE';
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.subcategoryManagerService.getSubcategoryList());
  }

  deleteSubcategory(subcategory: any): void {
    const modalRef = this.ModalService.open(ConfirmDeleteComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    modalRef.componentInstance.subcategory = subcategory;
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.subcategoryManagerService.getSubcategoryList());
  }

  updateSubcategory(subcategory: any): void {
    const modalRef = this.ModalService.open(AddSubcategoryComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    modalRef.componentInstance.subcategory = subcategory;
    modalRef.componentInstance.operation = 'UPDATE';
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.subcategoryManagerService.getSubcategoryList());
  }
}
